    }
    .toolbar__logo{

margin-left: 0;

    }}

 
.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
  }
  
  .toggle-button:focus {
    outline: none;
  }
  
  .toggle-button__line {
    width: 30px;
    height: 2px;
    background: white;
  }
.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
  }

  .side-drawer.open {
    transform: translateX(0);
  }


  .side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .side-drawer li {

    margin: 0.5rem 0;
  }
  .side-drawer a {

    color: #521751;
    text-decoration: none;
    font-size: 1.2rem;
  }

  .side-drawer a:hover,
  .side-drawer a:active {
    color: #fa923f;
    
  }
 
  @media (min-width: 796px) {
    .side-drawer{

        display: none;
    }

  }
.backdrop {

position: fixed;
width: 100%;
height: 100%;
top: 0;
left: 0;
background: rgba(0,0,0,0.3);
z-index: 100;
}
